export default function AddonStorePage() {
  const pathname = usePathname()
  const [addons, setAddons] = useState<Addon[]>(mockAddons)
  const [searchQuery, setSearchQuery] = useState('')
  const [selectedCategory, setSelectedCategory] = useState('all')
  const [sortBy, setSortBy] = useState('popular')
  const [priceFilter, setPriceFilter] = useState('all')
  const [showDetailModal, setShowDetailModal] = useState(false)
  const [selectedAddon, setSelectedAddon] = useState<Addon | null>(null)
  const [installing, setInstalling] = useState<string | null>(null)

  // Filter and sort add-ons
  const filteredAddons = useMemo(() => {
    let filtered = addons

    // Search filter
    if (searchQuery) {
      const query = searchQuery.toLowerCase()
      filtered = filtered.filter(
        addon =>
          addon.name.toLowerCase().includes(query) ||
          addon.description.toLowerCase().includes(query) ||
          addon.category.toLowerCase().includes(query)
      )
    }

    // Category filter
    if (selectedCategory !== 'all') {
      filtered = filtered.filter(addon => addon.category === selectedCategory)
    }

    // Price filter
    if (priceFilter === 'free') {
      filtered = filtered.filter(addon => addon.price === 0)
    } else if (priceFilter === 'paid') {
      filtered = filtered.filter(addon => addon.price > 0)
    }

    // Sort
    switch (sortBy) {
      case 'popular':
        filtered = [...filtered].sort((a, b) => b.installs - a.installs)
        break
      case 'rating':
        filtered = [...filtered].sort((a, b) => b.rating - a.rating)
        break
      case 'newest':
        filtered = [...filtered].sort((a, b) => new Date(b.lastUpdated).getTime() - new Date(a.lastUpdated).getTime())
        break
      case 'price-low':
        filtered = [...filtered].sort((a, b) => a.price - b.price)
        break
      case 'price-high':
        filtered = [...filtered].sort((a, b) => b.price - a.price)
        break
      case 'name':
        filtered = [...filtered].sort((a, b) => a.name.localeCompare(b.name))
        break
    }

    return filtered
  }, [addons, searchQuery, selectedCategory, sortBy, priceFilter])

  // Featured add-ons
  const featuredAddons = addons.filter(addon => addon.featured && !addon.installed).slice(0, 3)

  // Handle install/uninstall
  const handleInstall = async (addonId: string) => {
    setInstalling(addonId)
    
    // Simulate installation
    await new Promise(resolve => setTimeout(resolve, 2000))
    
    setAddons(prev => prev.map(addon => {
      if (addon.id === addonId) {
        return { ...addon, installed: !addon.installed }
      }
      return addon
    }))
    
    setInstalling(null)
    
    if (selectedAddon?.id === addonId) {
      setSelectedAddon(prev => prev ? { ...prev, installed: !prev.installed } : null)
    }
  }

  // Render star rating
  const renderStars = (rating: number) => {
    return (
      <div className="flex items-center gap-0.5">
        {[1, 2, 3, 4, 5].map(star => (
          <svg
            key={star}
            className={`w-4 h-4 ${star <= Math.round(rating) ? 'text-amber-400' : 'text-slate-300'}`}
            fill="currentColor"
            viewBox="0 0 20 20"
          >
            <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
          </svg>
        ))}
      </div>
    )
  }

  // Get installed count
  const installedCount = addons.filter(a => a.installed).length

  return (
    <div className="space-y-6 pb-8">
      {/* Page Header with Tabs */}
      <div className="flex-none bg-white border-b border-slate-200 -mx-4 sm:-mx-6 lg:-mx-8 -mt-4 sm:-mt-6 lg:-mt-8 px-4 sm:px-6 lg:px-8 pt-4 sm:pt-6 lg:pt-8 pb-0 mb-8">
        <PageHeader
          title="Addon Store"
          description="Extend your CRM with powerful add-ons and integrations"
          icon="fa-store"
          actions={
            <div className="flex items-center gap-3">
              <div className="px-4 py-2 bg-emerald-50 text-emerald-700 rounded-xl text-sm font-medium">
                {installedCount} Add-ons Installed
              </div>
            </div>
          }
        />
        
        {/* Tab Navigation */}
        <nav className="flex items-center gap-1 overflow-x-auto -mb-px">
          {STORE_TABS.map(tab => (
            <Link
              key={tab.id}
              href={tab.href}
              className={`px-4 py-3 text-sm font-medium border-b-2 transition-colors whitespace-nowrap ${
                tab.id === 'browse'
                  ? 'border-emerald-500 text-emerald-600'
                  : 'border-transparent text-slate-500 hover:text-slate-700 hover:border-slate-300'
              }`}
            >
              {tab.label}
              {tab.id === 'installed' && (
                <span className="ml-2 px-2 py-0.5 bg-slate-100 text-slate-600 rounded-full text-xs">
                  {installedCount}
                </span>
              )}
            </Link>
          ))}
        </nav>
      </div>

      {/* Featured Add-ons Banner */}
      {featuredAddons.length > 0 && (
        <div className="bg-gradient-to-r from-emerald-500 via-teal-500 to-cyan-500 rounded-2xl p-6 text-white">
          <h3 className="text-lg font-semibold mb-4">Featured Add-ons</h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            {featuredAddons.map(addon => (
              <button
                key={addon.id}
                onClick={() => {
                  setSelectedAddon(addon)
                  setShowDetailModal(true)
                }}
                className="bg-white/10 backdrop-blur-sm rounded-xl p-4 text-left hover:bg-white/20 transition-colors"
              >
                <div className="flex items-center gap-3 mb-2">
                  <span className="text-2xl">{addon.icon}</span>
                  <div>
                    <h4 className="font-semibold">{addon.name}</h4>
                    <p className="text-sm text-white/80">
                      {addon.id === 'addon-ai-pack' ? 'Custom Bundle' : addon.price === 0 ? 'Free' : `$${addon.price}${addon.priceUnit ? `/${addon.priceUnit}` : ''}/mo`}
                    </p>
                  </div>
                </div>
                <p className="text-sm text-white/80 line-clamp-2">{addon.description}</p>
              </button>
            ))}
          </div>
        </div>
      )}

      {/* Search and Filters */}
      <div className="flex flex-col lg:flex-row gap-4">
        {/* Search */}
        <div className="flex-1 relative">
          <svg
            className="w-5 h-5 text-slate-400 absolute left-4 top-1/2 -translate-y-1/2"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
          </svg>
          <input
            type="text"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            placeholder="Search add-ons..."
            className="w-full pl-12 pr-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-emerald-500 focus:border-transparent"
          />
        </div>

        {/* Filters */}
        <div className="flex gap-3">
          <select
            value={priceFilter}
            onChange={(e) => setPriceFilter(e.target.value)}
            className="px-4 py-3 border border-slate-300 rounded-xl text-sm font-medium focus:outline-none focus:ring-2 focus:ring-emerald-500 bg-white"
          >
            <option value="all">All Prices</option>
            <option value="free">Free</option>
            <option value="paid">Paid</option>
          </select>
          
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            className="px-4 py-3 border border-slate-300 rounded-xl text-sm font-medium focus:outline-none focus:ring-2 focus:ring-emerald-500 bg-white"
          >
            <option value="popular">Most Popular</option>
            <option value="rating">Highest Rated</option>
            <option value="newest">Newest</option>
            <option value="price-low">Price: Low to High</option>
            <option value="price-high">Price: High to Low</option>
            <option value="name">Name A-Z</option>
          </select>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex gap-6">
        {/* Categories Sidebar */}
        <div className="hidden lg:block w-64 flex-shrink-0">
          <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-4 sticky top-24">
            <h3 className="font-semibold text-slate-900 mb-4">Categories</h3>
            <div className="space-y-1">
              {CATEGORIES.map(category => (
                <button
                  key={category.id}
                  onClick={() => setSelectedCategory(category.id)}
                  className={`w-full flex items-center gap-3 px-3 py-2.5 rounded-xl text-left transition-colors ${
                    selectedCategory === category.id
                      ? 'bg-emerald-50 text-emerald-700'
                      : 'text-slate-600 hover:bg-slate-50'
                  }`}
                >
                  <span className="text-lg">{category.icon}</span>
                  <span className="text-sm font-medium">{category.label}</span>
                  <span className="ml-auto text-xs text-slate-400">
                    {category.id === 'all'
                      ? addons.length
                      : addons.filter(a => a.category === category.id).length}
                  </span>
                </button>
              ))}
            </div>
          </div>
        </div>

        {/* Add-ons Grid */}
        <div className="flex-1">
          {/* Mobile Category Filter */}
          <div className="lg:hidden mb-4">
            <select
              value={selectedCategory}
              onChange={(e) => setSelectedCategory(e.target.value)}
              className="w-full px-4 py-3 border border-slate-300 rounded-xl text-sm font-medium focus:outline-none focus:ring-2 focus:ring-emerald-500 bg-white"
            >
              {CATEGORIES.map(category => (
                <option key={category.id} value={category.id}>
                  {category.icon} {category.label}
                </option>
              ))}
            </select>
          </div>

          {/* Results Count */}
          <p className="text-sm text-slate-500 mb-4">
            Showing {filteredAddons.length} of {addons.length} add-ons
          </p>

          {/* Grid */}
          {filteredAddons.length > 0 ? (
            <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-4">
              {filteredAddons.map(addon => (
                <div
                  key={addon.id}
                  className="bg-white rounded-2xl shadow-sm border border-slate-200 p-5 hover:shadow-md transition-shadow"
                >
                  {/* Header */}
                  <div className="flex items-start gap-4 mb-4">
                    <div className="w-14 h-14 bg-slate-100 rounded-xl flex items-center justify-center text-2xl flex-shrink-0">
                      {addon.icon}
                    </div>
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center gap-2">
                        <h3 className="font-semibold text-slate-900 truncate">{addon.name}</h3>
                        {addon.new && (
                          <span className="px-2 py-0.5 bg-blue-100 text-blue-700 text-xs font-medium rounded-full">
                            New
                          </span>
                        )}
                      </div>
                      <p className="text-sm text-slate-500">{addon.developer}</p>
                    </div>
                  </div>

                  {/* Description */}
                  <p className="text-sm text-slate-600 mb-4 line-clamp-2">{addon.description}</p>

                  {/* Stats */}
                  <div className="flex items-center gap-4 mb-4 text-sm">
                    <div className="flex items-center gap-1">
                      {renderStars(addon.rating)}
                      <span className="text-slate-600 ml-1">{addon.rating}</span>
                    </div>
                    <span className="text-slate-400">|</span>
                    <span className="text-slate-600">{addon.installs.toLocaleString()} installs</span>
                  </div>

                  {/* Footer */}
                  <div className="flex items-center justify-between pt-4 border-t border-slate-100">
                    <div>
                      {addon.id === 'addon-ai-pack' ? (
                        <span className="text-lg font-bold text-purple-600">Custom Bundle</span>
                      ) : addon.price === 0 ? (
                        <span className="text-lg font-bold text-emerald-600">Free</span>
                      ) : (
                        <div>
                          <span className="text-lg font-bold text-slate-900">${addon.price}</span>
                          <span className="text-sm text-slate-500">{addon.priceUnit ? `/${addon.priceUnit}` : ''}/mo</span>
                        </div>
                      )}
                    </div>
                    <div className="flex items-center gap-2">
                      <button
                        onClick={() => {
                          setSelectedAddon(addon)
                          setShowDetailModal(true)
                        }}
                        className="px-4 py-2 text-sm font-medium text-slate-600 hover:bg-slate-50 rounded-lg transition-colors"
                      >
                        Details
                      </button>
                      <button
                        onClick={() => handleInstall(addon.id)}
                        disabled={installing === addon.id}
                        className={`px-4 py-2 text-sm font-medium rounded-lg transition-all ${
                          addon.installed
                            ? 'bg-slate-100 text-slate-700 hover:bg-slate-200'
                            : 'bg-gradient-to-r from-emerald-500 to-teal-500 text-white hover:from-emerald-600 hover:to-teal-600 shadow-lg'
                        } disabled:opacity-50`}
                      >
                        {installing === addon.id ? (
                          <svg className="animate-spin w-4 h-4" fill="none" viewBox="0 0 24 24">
                            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                          </svg>
                        ) : addon.installed ? (
                          'Installed'
                        ) : (
                          'Install'
                        )}
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="bg-white rounded-2xl shadow-sm border border-slate-200 p-12 text-center">
              <div className="w-16 h-16 bg-slate-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-8 h-8 text-slate-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
              </div>
              <h3 className="text-lg font-semibold text-slate-900 mb-2">No add-ons found</h3>
              <p className="text-slate-500">Try adjusting your search or filters</p>
            </div>
          )}
        </div>
      </div>

      {/* Detail Modal */}
      {showDetailModal && selectedAddon && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-2xl w-full shadow-2xl overflow-hidden max-h-[90vh] flex flex-col">
            {/* Header */}
            <div className="bg-gradient-to-r from-emerald-500 to-teal-500 px-6 py-4 flex items-center justify-between flex-shrink-0">
              <div className="flex items-center gap-4">
                <div className="w-12 h-12 bg-white/20 rounded-xl flex items-center justify-center backdrop-blur-sm text-2xl">
                  {selectedAddon.icon}
                </div>
                <div className="text-white">
                  <h3 className="text-lg font-semibold">{selectedAddon.name}</h3>
                  <p className="text-sm text-emerald-100">by {selectedAddon.developer}</p>
                </div>
              </div>
              <button
                onClick={() => {
                  setShowDetailModal(false)
                  setSelectedAddon(null)
                }}
                className="text-white hover:bg-white/20 rounded-lg p-2 transition-colors"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            {/* Content */}
            <div className="p-6 overflow-y-auto flex-1">
              {/* Stats Bar */}
              <div className="flex items-center gap-6 pb-4 border-b border-slate-200 mb-6">
                <div className="flex items-center gap-2">
                  {renderStars(selectedAddon.rating)}
                  <span className="font-semibold text-slate-900">{selectedAddon.rating}</span>
                  <span className="text-slate-500">({selectedAddon.reviews} reviews)</span>
                </div>
                <span className="text-slate-300">|</span>
                <span className="text-slate-600">{selectedAddon.installs.toLocaleString()} installs</span>
                <span className="text-slate-300">|</span>
                <span className="text-slate-600">v{selectedAddon.version}</span>
              </div>

              {/* Description */}
              <div className="mb-6">
                <h4 className="font-semibold text-slate-900 mb-2">Description</h4>
                <p className="text-slate-600 leading-relaxed">{selectedAddon.longDescription || selectedAddon.description}</p>
              </div>

              {/* Features */}
              <div className="mb-6">
                <h4 className="font-semibold text-slate-900 mb-3">Features</h4>
                <div className="grid grid-cols-2 gap-2">
                  {selectedAddon.features.map((feature, index) => (
                    <div key={index} className="flex items-center gap-2">
                      <svg className="w-5 h-5 text-emerald-500 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                      </svg>
                      <span className="text-sm text-slate-600">{feature}</span>
                    </div>
                  ))}
                </div>
              </div>

              {/* Info */}
              <div className="bg-slate-50 rounded-xl p-4 mb-6">
                <div className="grid grid-cols-2 gap-4 text-sm">
                  <div>
                    <p className="text-slate-500">Category</p>
                    <p className="font-medium text-slate-900 capitalize">{selectedAddon.category.replace('-', ' ')}</p>
                  </div>
                  <div>
                    <p className="text-slate-500">Last Updated</p>
                    <p className="font-medium text-slate-900">{selectedAddon.lastUpdated}</p>
                  </div>
                  <div>
                    <p className="text-slate-500">Version</p>
                    <p className="font-medium text-slate-900">{selectedAddon.version}</p>
                  </div>
                  <div>
                    <p className="text-slate-500">Developer</p>
                    <p className="font-medium text-slate-900">{selectedAddon.developer}</p>
                  </div>
                </div>
              </div>

              {/* Pricing */}
              <div className={`${selectedAddon.id === 'addon-ai-pack' ? 'bg-purple-50' : 'bg-emerald-50'} rounded-xl p-4`}>
                <div className="flex items-center justify-between">
                  <div>
                    {selectedAddon.id === 'addon-ai-pack' ? (
                      <div>
                        <p className="text-2xl font-bold text-purple-600">Custom Bundle</p>
                        <p className="text-sm text-slate-500">5% discount per AI tool added</p>
                      </div>
                    ) : selectedAddon.price === 0 ? (
                      <p className="text-2xl font-bold text-emerald-600">Free</p>
                    ) : (
                      <div>
                        <span className="text-2xl font-bold text-slate-900">${selectedAddon.price}</span>
                        <span className="text-slate-500">{selectedAddon.priceUnit ? ` per ${selectedAddon.priceUnit}` : ''}/month</span>
                      </div>
                    )}
                    {selectedAddon.price > 0 && selectedAddon.id !== 'addon-ai-pack' && (
                      <p className="text-sm text-slate-500">Billed monthly ‚Ä¢ Cancel anytime</p>
                    )}
                  </div>
                  <button
                    onClick={() => handleInstall(selectedAddon.id)}
                    disabled={installing === selectedAddon.id}
                    className={`px-6 py-3 text-sm font-medium rounded-xl transition-all ${
                      selectedAddon.installed
                        ? 'bg-white text-slate-700 hover:bg-slate-100 border border-slate-200'
                        : 'bg-gradient-to-r from-emerald-500 to-teal-500 text-white hover:from-emerald-600 hover:to-teal-600 shadow-lg'
                    } disabled:opacity-50`}
                  >
                    {installing === selectedAddon.id ? (
                      <span className="flex items-center gap-2">
                        <svg className="animate-spin w-4 h-4" fill="none" viewBox="0 0 24 24">
                          <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                          <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                        </svg>
                        Processing...
                      </span>
                    ) : selectedAddon.installed ? (
                      'Uninstall'
                    ) : selectedAddon.id === 'addon-ai-pack' ? (
                      'Build Your Pack'
                    ) : selectedAddon.price === 0 ? (
                      'Install Now'
                    ) : (
                      'Subscribe & Install'
                    )}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
